<template>
  <form class="row g-3 myform">
    <h1>REQUEST</h1>
    <div class="col-12">
      <label for="inputAddress" class="form-label">room id</label>
      <input type="text" class="form-control" id="inputAddress" placeholder="input room id" v-model="room">
    </div>
    <div class="col-12">
      <label for="inputAddress2" class="form-label">emit name</label>
      <div class="alert alert-danger d-flex align-items-center" role="alert">
        <svg class="bi flex-shrink-0 me-2" width="24" height="24" role="img" aria-label="Danger:"><use xlink:href="#exclamation-triangle-fill"/></svg>
        <div>
          WARNING!!! without "on_"
        </div>
      </div>
      <input type="text" class="form-control" id="inputAddress2" placeholder="input emit on server" v-model="emit_name">
    </div>
    <div class="col-12">
      <label for="inputAddress2" class="form-label">JSON message</label>
      <v-jsoneditor v-model="json" :options="options" :plus="false" height="400px" @error="onError"></v-jsoneditor>
    </div>
    <div class="col-12">
      <button type="button" class="btn btn-primary" v-on:click="sendMessage" >send</button>
    </div>
  </form>    
</template>

<script>

export default {
    data() {
        return {
          emit_name: null,
          room: null,
          json: {
            hello: "vue",
          },
          options: {
            mode: "code",
          }
        }
    },
    created() {
        setTimeout(() => this.room = this.$store.getters.get_room, 1000);
    },
    methods: {
        onError() {
            console.log('error')
        },
        sendMessage() {
          this.json.room = this.room;
          //this.json.emit_name = this.emit_name;
          this.$socket.emit(this.emit_name, this.json);
        }
    }
}
</script>

<style scoped>
.myform {
    max-width: 95%;
    margin: 0 auto;
    background-color: white;
    padding: 1em;
    border-radius: 5px;
}
</style>