<template>
    <form class="row g-3 myresp">
      <h1>RESPONSE</h1>
  <div class="col-12">
    <label for="inputAddress2" class="form-label">JSON data</label>
    <v-jsoneditor v-model="response" :options="options" :plus="true" height="600px" @error="onError"></v-jsoneditor>
  </div>
  <div class="col-12">
    <button type="button" class="btn btn-primary" v-on:click="clear">clear</button>
    <button type="button" class="btn" v-on:click="mode_change">mode</button>
  </div>
</form>    
</template>

<script>
export default {
    data() {
        return {
            options: {
              mode: "tree",
            },
            mod: false
        }
    },
    created(){
      
    },
    methods: {
      onError() {
        console.log('error');
      },
      clear(){
        this.$store.commit('set_response_value', null);
      },
      mode_change() {
        this.mod = !this.mod;
        if (this.mod) this.options.mode = "code";
        else this.options.mode = "tree";
      }
    },
    computed:{
      response() {
        return this.$store.getters.get_response;
      }
    }
}
</script>

<style scoped>
.myresp {
    max-width: 95%;
    margin: 0 auto;
    background-color: white;
    padding: 1em;
    border-radius: 5px;
}
</style>