import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap"
import VueSocketIO from 'vue-socket.io'
import SocketIO from 'socket.io-client'
import Vue from 'vue'
import VJsoneditor from 'v-jsoneditor/src/index'
import App from './App.vue'
import Vuex from 'vuex'

Vue.config.productionTip = false
Vue.use(new VueSocketIO({
    debug: true,
    connection: SocketIO('http://localhost:5000/queue'), //options object is Optional
}))
Vue.use(VJsoneditor)
Vue.use(Vuex)


const store = new Vuex.Store({
    state: {
        response: null,
        room: null,
    },
    mutations: {
        set_response_value(state, value) {
            state.response = value;
        },
        set_room_value(state, value) {
            state.room = value;
        }
    },
    getters: {
        get_response: state => {
            return state.response;
        },
        get_room: state => {
            return state.room;
        }
    }
})



new Vue({
    render: h => h(App),
    store: store,
}).$mount('#app')